.footer-contacts__address-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 27px 20px 25px;
  column-gap: 4px;
}

.footer-contacts__adress-wrapper--grey-theme {
  background-color: @basic-grey;
}

.footer-contacts__text {
  margin: 0;
  padding: 0;
  padding-right: 5px;
  color: #111111;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
}

.footer-contacts__address {
  font-family: "Lato", "Arial", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  text-align: left;
}

.footer-contacts__city {
  font-size: inherit;
  line-height: inherit;
}

.footer-contacts__map-container {
  min-height: 361px;
  position: relative;
}

.footer-contacts__map-wrapper,
.footer-contacts__nojs-wrapper {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
}

.footer-contacts__nojs-wrapper {
  &::after {
    content: "";
    width: 57px;
    height: 53px;
    background-image: url("../img/pin.png");
    background-repeat: no-repeat;
    position: absolute;
    z-index: 2;
    top: 39%;
    left: calc(50% - 27px);

    @media (min-resolution: 2dppx) {
      background-image: url("../img/pin@2x.png");
      background-size: 57px 53px;
    }
  }
}

.footer-contacts__address-wrapper--grey {
  background-color: #f2f2f2f2;
}

@media (min-width: @tablet-width) {
  .footer-contacts__address-wrapper {
    column-gap: 302px;
    padding: 61px 70px;
  }

  .footer-contacts__address-wrapper--grey {
    background-color: #f2f2f2;
  }

  .footer-contacts__text {
    .zero-ph-styles();
    font-size: 20px;
    line-height: 26px;
    width: 163px;
  }

  .footer-contacts__address {
    text-align: right;
    font-size: 16px;
  }

  .footer-contacts__city {
    display: inline-block;
    margin-top: 19px;
  }

  .footer-contacts__map-container {
    min-height: 400px;
  }

  .footer-contacts__nojs-wrapper {
    &::after {
      background-image: url("../img/pin-tablet.png");
      width: 113px;
      height: 106px;
      top: 36%;
      left: calc(50% - 51px);
      background-size: 113px 106px;
    }
  }

  @media (min-resolution: 2dppx) {
    .footer-contacts__nojs-wrapper {
      &::after {
        background-image: url("../img/pin-tablet@2x.png");
        background-size: 113px 106px;
      }
    }
  }
}

@media (min-width: @desktop-width) {
  .footer-contacts {
    position: relative;
    background-color: #f2f2f2;
    padding: 97px 0 105px;//индекс
  }

  .footer-contacts__address-wrapper {
    width: 565px;
    background-color: #ffffff;
    column-gap: 87px;
    padding: 61px 78px 60px 80px;
    position: relative;
    margin-top: 0;
    z-index: 5;
    top: 0;
    left: 50%;
    bottom: 10px;
    margin-left: -610px;
  }

  .footer-contacts__map-container {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: 0;
    bottom: 0;
  }

  .footer-contacts__address {
    text-align: left;
  }

  .footer-contacts__nojs-wrapper::after {
    top: 32%;
    left: calc(50% + 188px);
  }
}
