.radio__label {
  color: #444444;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  text-transform: uppercase;
  display: inline-block;
  padding: 7px 9px 7px 45px;
  position: relative;
  min-height: 32px;
  cursor: pointer;
}
.radio__label::before {
  content: "";
  width: 30px;
  height: 30px;
  position: absolute;
  top: 0;
  left: 0;
  border: 1px solid #cdcdcd;
  border-radius: 50%;
}
.radio__label::after {
  content: "";
  display: none;
  width: 13px;
  height: 13px;
  position: absolute;
  top: 9px;
  left: 9px;
  background-color: #68b738;
  border-radius: 50%;
}
.radio__input:checked + .radio__label::after {
  display: block;
}
.radio__input:hover + .radio__label::before,
.radio__input:focus + .radio__label::before {
  border: 1px solid #bababa;
}
.radio__input:focus-visible + .radio__label::before {
  outline: 1px solid #000000;
}
.radio__input:disabled + .radio__label::before {
  border-color: rgba(186, 186, 186, 0.3);
}
.radio__input:disabled + .radio__label {
  color: rgba(0, 0, 0, 0.3);
}
.radio__input:checked:disabled + .radio__label::after {
  opacity: 0.3;
}
.radio__input:active + .radio__label::before {
  border-color: rgba(186, 186, 186, 0.6);
}
.radio__input:active + .radio__label {
  color: rgba(0, 0, 0, 0.6);
}
.radio__input:checked:active + .radio__label::after {
  opacity: 0.6;
}
@media (min-width: 768px) {
  .radio__label {
    font-size: 20px;
    padding: 7px 9px 7px 61px;
  }
  .radio__label::before {
    width: 40px;
    height: 40px;
    top: -4px;
    left: 1px;
  }
  .radio__label::after {
    width: 18px;
    height: 18px;
    top: 7px;
    left: 12px;
  }
}
@media (min-width: 1440px) {
  .radio__label::after {
    width: 18px;
    height: 18px;
    top: 8px;
    left: 12px;
  }
}
.checkbox__label {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  text-transform: uppercase;
  position: relative;
  padding-left: 45px;
  display: inline-block;
  min-height: 32px;
  cursor: pointer;
}
.checkbox__label::before {
  content: "";
  display: block;
  width: 30px;
  height: 30px;
  border: 1px solid #cdcdcd;
  position: absolute;
  top: -7px;
  left: 0;
}
.checkbox__label::after {
  content: "";
  width: 18px;
  height: 18px;
  display: none;
  background-image: url("../img/icons/sprite.svg#checkbox-tick");
  background-repeat: no-repeat;
  background-position: center;
  position: absolute;
  top: -1px;
  left: 6px;
}
.checkbox__input:checked + .checkbox__label::after {
  display: block;
}
.checkbox__input:hover + .checkbox__label::before,
.checkbox__input:focus + .checkbox__label::before {
  border: 1px solid #bababa;
}
.checkbox__input:focus-visible + .checkbox__label::before {
  outline: 1px solid #000000;
}
.checkbox__input:disabled + .checkbox__label::before {
  border-color: rgba(186, 186, 186, 0.3);
}
.checkbox__input:disabled + .checkbox__label {
  color: rgba(0, 0, 0, 0.3);
}
.checkbox__input:checked:disabled + .checkbox__label::after {
  opacity: 0.3;
}
.checkbox__input:active + .checkbox__label::before {
  border-color: rgba(186, 186, 186, 0.6);
}
.checkbox__input:active + .checkbox__label {
  color: rgba(0, 0, 0, 0.6);
}
.checkbox__input:checked:active + .checkbox__label::after {
  opacity: 0.6;
}
@media (min-width: 768px) {
  .checkbox__label {
    font-size: 20px;
    line-height: normal;
    padding-left: 60px;
  }
  .checkbox__label::before {
    width: 40px;
    height: 40px;
  }
  .checkbox__label::after {
    width: 26px;
    height: 26px;
  }
}
