/*header-nav--opened открывает меню>*/
/*header-nav--closed скрывает меню*/
.header-nav__toggle {
  width: 24px;
  height: 24px;
  border: none;
  position: absolute;
  right: 21px;
  top: -36px;
  background-color: transparent;
  cursor: pointer;
  display: none;
}
.header-nav--closed .header-nav__toggle::before {
  content: "";
  display: block;
  width: 100%;
  height: 2px;
  background-color: #000000;
  position: absolute;
  top: 0;
  left: 0;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
.header-nav--opened .header-nav__toggle {
  width: 24px;
  right: 17px;
  top: -40px;
  display: block;
}
.header-nav--opened .header-nav__toggle::before,
.header-nav--opened .header-nav__toggle::after {
  content: "";
  display: block;
  width: 25px;
  height: 2px;
  background-color: #000000;
  position: absolute;
  left: 0;
  top: 50%;
  box-shadow: none;
}
.header-nav--opened .header-nav__toggle::before {
  transform: rotate(45deg);
}
.header-nav--opened .header-nav__toggle::after {
  transform: rotate(-45deg);
}
@media (max-width: 767px) {
